<template>
  <nav v-if="stepsInfo" :class="['p-steps', { 'full-screen': !isPreview }]">
    <AntSteps :current="stepsInfo.current - 1" :items="steps" :responsive="false" />
  </nav>
</template>

<script lang="ts" setup>
import { Steps as AntSteps } from 'ant-design-vue';
import { computed } from 'vue';

const props = defineProps<{
  stepsInfo?: { current: number; total: number } | null;
  isPreview: boolean;
}>();

const steps = computed(() =>
  props.stepsInfo
    ? Array(props.stepsInfo.total)
        .fill(null)
        .map(() => ({ label: '', description: '' }))
    : [],
);
</script>

<style scoped lang="scss">
@import '@/styles/index.scss';

.p-steps {
  margin: 20px auto;
  position: relative;
  padding: 0;
  list-style-type: none;
  display: flex;
  width: calc(100% - 40px);

  &.full-screen {
    width: 600px;
  }
}
</style>
